import React from "react"
import moment from "moment"
import Layout from "../components/Layout"
import styled from "styled-components"
import { ExternalLinkArrow } from "../styles/GlobalStyle"
import usePrivacy from "../hooks/usePrivacy"
import { documentToReactComponents } from "@contentful/rich-text-react-renderer"

const Privacy = () => {
  const privacyData = usePrivacy()[0].node

  return (
    <Layout headerStyle={{ position: "absolute", width: "100%", top: "20px" }}>
      <InnterContent>
        <Title>{privacyData.title.title}</Title>
        <Date>
          Updated {moment(privacyData.updatedAt).format("MMMM Do YYYY")}
        </Date>
        {documentToReactComponents(privacyData.content.json)}
      </InnterContent>
    </Layout>
  )
}

export default Privacy

const Title = styled.h1`
  font-family: "Inter-Medium";
  font-weight: 600;
  text-align: center;
  max-width: 450px;
  margin: 0 auto 60px auto;
  font-size: 50px;
  font-weight: 600;
  font-family: "Inter-Medium";
  color: ${props => props.theme.colors.primary};
  ${({ theme }) => theme.mediaWidth.upToSmall`
   font-size:35px;
   `}
`

const InnterContent = styled.div`
  max-width: 1024px;
  margin: 0 auto 80px auto;
  padding-top: 180px;
  padding-right: 20px;
  padding-left: 20px;

  p {
    line-height: 1.8 !important;
    margin-bottom: 0 !important;

    b {
      margin-top: 20px !important;
      color: ${props => props.theme.colors.primary};
      font-family: "Inter-Medium";
    }
  }
  p,
  li {
    color: ${props => props.theme.colors.secondary};
    font-weight: bold;

    ${({ theme }) => theme.mediaWidth.upToSmall`
  font-size:15px;
  `}
    a {
      ${ExternalLinkArrow}
      display:inline;

      word-break: break-all;
    }
  }

  h3 {
    font-size: 23px;
    font-family: "Inter-Medium";
    color: ${props => props.theme.colors.primary};
    margin-top: 60px;
    margin-bottom: 20px;
    ${({ theme }) => theme.mediaWidth.upToSmall`
  font-size:20px;
  margin-bottom:20px;
  `}
  }
`

